import React, { useState, useEffect } from "react";
import "../css/answer.scss";
import { XMLParser } from "fast-xml-parser";
import { fetchGistRawContent } from "../../../services";
import "../css/examanswers.scss";
import Plus from "../../../img/svg/plusminus.svg";
import Check from "../../../img/svg/check.svg";
import Dot from "../../../img/svg/dot.svg";
import Header from "../../../components/header";

export default function Answer() {
  const [studentsList, setStudentsList] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [notFoundError, setNotFoundError] = useState(false);

  useEffect(() => {
    async function fetchStudents() {
      try {
        const grade_11 = await fetchGistRawContent(
          "ddb2b20ab7665a82f7dcc04905da2c10",
          "11_sinif.xml"
        );
        const grade_9 = await fetchGistRawContent(
          "33fd8dae38c014963bb6c9afb21da424",
          "9_sinif.xml"
        );

        const students11 = parseStudents(grade_11);
        const students9 = parseStudents(grade_9);

        const combinedStudentsList = [...students11, ...students9];
        setStudentsList(combinedStudentsList);
      } catch (error) {
        console.error("Error fetching student data:", error);
        if (error.message.includes("403")) {
          // Handle 403 Forbidden error
          // For example, show a message to the user
          alert(
            "Access to student data is forbidden. Please contact the administrator for assistance."
          );
        }
      }
    }

    fetchStudents();
  }, []); // Run this effect only once on component mount

  function parseStudents(xmlStudentsList) {
    const parser = new XMLParser();
    const parsedXml = parser.parse(xmlStudentsList);
    const { dataroot } = parsedXml;
    const studentsList = dataroot[Object.keys(dataroot)[0]];

    return studentsList;
  }

  function findStudent() {
    const code = document.getElementById("code").value.trim();
    const grade = document.getElementById("grade").value;

    let targetStudent = null;
    if (grade === "9") {
      targetStudent = studentsList.find(
        (s) => s["_nmrsi"] === +code && s["Sinif"] == grade
      );
    } else {
      targetStudent = studentsList.find((s) => s["_nmrsi"] === +code);
    }

    setNotFoundError(!targetStudent);
    setSelectedStudent(targetStudent);
  }

  function handleKeyPress(event) {
    if (event.key === "Enter") {
      findStudent();
    }
  }

  return (
    <>
      <Header />
      <div className="exam__answer">
        <div className="exam__container">
          <h2>18 fevral imtahan nticlri n i nmrsini daxil edin</h2>
          <input
            type="text"
            id="code"
            name="code"
            required
            onKeyPress={handleKeyPress}
          />
          <div className="exam__container--btn">
            <select name="grade" id="grade">
              <option value="9" label="9 Sinif" />
              <option value="10-11" label="10/11 sinif" />
            </select>
            <button type="button" onClick={findStudent}>
              Nticni gstr
            </button>
          </div>
          {selectedStudent && (
            <div id="result" className="exam__result">
              <div className="exam__info">
                <div className="exam__info--name">
                  <div className="name">
                    <span>Ad:</span>
                    {selectedStudent.Ad &&
                      selectedStudent.Ad.charAt(0).toUpperCase() +
                        selectedStudent.Ad.slice(1).toLowerCase()}
                  </div>
                  <div className="surname">
                    <span>Soyad:</span>
                    {selectedStudent.Soyad &&
                      selectedStudent.Soyad.charAt(0).toUpperCase() +
                        selectedStudent.Soyad.slice(1).toLowerCase()}
                  </div>
                </div>
                <div className="exam__info--about">
                  <div>
                    <span> nmrsi:</span>
                    {selectedStudent["_nmrsi"]}
                  </div>
                  <div>
                    <span>Mktb:</span>
                    {selectedStudent["Mktb"]}
                  </div>
                  <div>
                    <span>Sinif:</span>
                    {selectedStudent["Sinif"]}
                  </div>
                  <div>
                    <span>Variant:</span>
                    {selectedStudent["Variant"]}
                  </div>
                  <div>
                    <span>Blm:</span>
                    {selectedStudent["Blm"]}
                  </div>
                </div>
                <div className="exam__info--grade">
                  <div className="grade">
                    <h2>Bal:</h2>
                    <h2>{selectedStudent["BAL"]}</h2>
                  </div>
                  <hr />

                  <div className="place">
                    <h3>Sra:</h3>
                    <h3>{selectedStudent["SIRA"]}</h3>
                  </div>
                </div>
              </div>
              <div className="exam__subject--item">
                <div className="exam__subject--item__name">
                  <h3>Ana dili</h3>
                  <h3>Riyaziyyat</h3>
                  <h3>Xarici dil</h3>
                </div>

                <div className="exam__subject">
                  <div className="exam__subject--native">
                    <h3>Qapal tipli testlr</h3>
                    <div className="exam__subject--native__container">
                      <div className="exam__subject--native__close">
                        <h4>Ana dili</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Ana_dili_Cavabnz"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Ana_dili_Doru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Ana_dili_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Riyaziyyat</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Cavabnz"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Doru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Xarici dil</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Cavabnz"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Doru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Status"]}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="exam__subject--matem">
                    <hr />
                    <h3>Aq tipli testlr</h3>
                    <div className="exam__subject--matem__container">
                      <div className="exam__subject--native__open">
                        <h4></h4>
                      </div>
                      <div className="exam__subject--native__open">
                        <h4>Riyaziyyat</h4>
                        <div className="native__write">
                          {selectedStudent["Riyaziyyat_Aq_test_Cavabnz"]}
                        </div>
                        <div className="native__write">
                          {
                            selectedStudent[
                              "Riyaziyyat_Aq_test_Doru_cavablar"
                            ]
                          }
                        </div>
                        <div className="native__write">
                          {selectedStudent["Riyaziyyat_Aq_test_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__write">
                        <h4></h4>
                        <div className="native__write"></div>
                      </div>
                    </div>
                  </div>
                  <div className="exam__subject--foreign">
                    <hr />
                    <h3>Yaz ii zr ballar</h3>
                    <div className="exam__subject--foreign__container">
                      <div className="exam__subject--native__write">
                        <h4>Ana dili</h4>
                        <div className="native__write">
                          {selectedStudent["Ana_dili_Yaz_ii_ballar"]}
                        </div>
                      </div>
                      <div className="exam__subject--foreign__open">
                        <h4>Riyaziyyat</h4>
                        <div className="foreign__write">
                          {selectedStudent["Riyaziyyat_Yaz_ii_ballar"]}
                        </div>
                      </div>
                      <div className="exam__subject--foreign__write">
                        <h4>Xarici dil</h4>
                        <div className="foreign__write">
                          {selectedStudent["Xarici_dil_Yaz_ii_ballar"]}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <p>Qeyd: Yaz ii zr ballar 6-ya blnrk hesablanmdr</p>
              <table>
                <thead>
                  <tr>
                    <th></th>
                    <th>Ana dili</th>
                    <th>Riyaziyyat</th>
                    <th>Xarici dil</th>
                    <th>CM</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th>Sual say</th>
                    <td>{selectedStudent["Ana_dili_Sual_say"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Sual_say"]}</td>
                    <td>{selectedStudent["Xarici_dil_Sual_say"]}</td>
                    <td>{selectedStudent["CM_SUAL_SAYI"]}</td>
                  </tr>
                  <tr>
                    <th>DORU</th>
                    <td>{selectedStudent["Ana_dili_Doru_say"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Doru_say"]}</td>
                    <td>{selectedStudent["Xarici_dil_Doru_say"]}</td>
                    <td>{selectedStudent["CM_DORU"]}</td>
                  </tr>
                  <tr>
                    <th>SHV</th>
                    <td>{selectedStudent["Ana_dili_Shv_say"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Shv_say"]}</td>
                    <td>{selectedStudent["Xarici_dil_Shv_say"]}</td>
                    <td>{selectedStudent["CM_SHV"]}</td>
                  </tr>
                  <tr>
                    <th>BAL</th>
                    <td>{selectedStudent["Ana_dili_Nisbi_bal"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Nisbi_bal"]}</td>
                    <td>{selectedStudent["Xarici_dil_Nisbi_bal"]}</td>
                    <td>{selectedStudent["BAL"]}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          )}
          {notFoundError && (
            <div className="error">Bu kodu olan tlb taplmad.</div>
          )}
        </div>
      </div>
    </>
  );
}
